import {api} from "../../utils/api"
const state={
    menuList:[],
    leftMenuList:[],
    carousel:[],
    mainData:[],
    isHome:true,

};
const mutations={
    MenuList(state,data){
        state.menuList=data;
    },
    LeftMenuList(state,data){
        state.leftMenuList=data;
    },
    Carousel(state,data){
        state.carousel=data;
    },
    MainData(state,data){
        state.mainData=data;
        state.mainData[0].nineList={
            img:['https://i8.mifile.cn/v1/a1/290976f3-acac-8a62-7e5c-a19985a6414b!144x152.webp',
                'https://i8.mifile.cn/v1/a1/eb5024fe-dfe3-6e53-3e18-675bef5fa06e!144x152.webp',
                 'https://i8.mifile.cn/v1/a1/e8bc849a-0a3b-21a0-6810-7da3a3903dee!144x152.webp',
                'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/c94c523eb03128378ef2dc17c2c2cf0a.png?thumb=1&w=144&h=152',
                'https://i8.mifile.cn/v1/a1/7dbcbf87-6a58-adb6-2f3f-bb3dae3e9c80!144x152.webp',
                 'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/cb65daec7ef7b52cc785f88f78efba37.png?thumb=1&w=144&h=152',
                'https://i8.mifile.cn/v1/a1/d5e3c742-3145-dd47-f665-331bef273a54!144x152.webp',
                'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/96c780016ea196743905dc93f9249c39.png?thumb=1&w=144&h=152',
                'https://i8.mifile.cn/v1/a1/d7058e48-c115-e651-b33c-04ce071ae9e6!144x152.webp',
                'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/f11f9df6b0b0b428f8c8fc3267131830.png?thumb=1&w=144&h=152'
            ]
        };
        for(let i=0;i<=6;i++){
            state.mainData.push({name:'出行'})
        }
    },
    IsHome(state,data){
        state.isHome=data
    },

};
const actions={
    getMenuList(context){
            api.get("/menu?type=top").then(response=>{
           context.commit("MenuList",response)
        })
    },
    getLeftMenuList(context){
        api.get("/menu?type=left").then(response=>{
            context.commit("LeftMenuList",response);
        });
    },
    getCarousel(context){
        api.get("/menu?type=carousel").then(response=>{
            context.commit("Carousel",response)
        })
    },
    getMainData(context){
      return  api.get("/menu?type=index").then(response=>{
            context.commit("MainData",response)
        })
    },
    getIsHome({commit},data){
      commit("IsHome",data)
    }
};
export default {
    namespaced:true,
    state,mutations,actions
}
